<template>
    <v-container>
        <v-card-text align="center"><font size="4"><b></b></font></v-card-text>
        <v-flex offset-xs1>
            <v-layout row>
            <p><font size="4"></font><font siz3="2">(.pdf)</font></p>
            <upload-button title="" name="introduction" accept=".pdf" :selectedCallback="Upload"></upload-button>
            </v-layout>
            <br>
            <v-layout row>
            <p><font size="4"></font><font siz3="2">(.pdf)</font></p>
            <upload-button title="" name="history" :selectedCallback="Upload"></upload-button>
            </v-layout>
            <br>
            <v-layout row>
            <p><font size="4"></font><font siz3="2">(.pdf)</font></p>
            <upload-button title="" name="envirnoment" :selectedCallback="Upload"></upload-button>
            </v-layout>
            <br>
            <v-layout row>
            <p><font size="4"></font><font siz3="2">(.pdf)</font></p>
            <upload-button title="" name="records" :selectedCallback="Upload"></upload-button>
            </v-layout>
            <br>
            <v-layout row>
            <p><font size="4">/</font><font siz3="2">(.pdf)</font></p>
            <upload-button title="" name="license" :selectedCallback="Upload"></upload-button>
            </v-layout>
            <br>
            <v-layout row>
            <p><font size="4"></font><font siz3="2">(.pdf)</font></p>
            <upload-button title="" name="product_report" :selectedCallback="Upload"></upload-button>
            </v-layout>
            <br>
            <v-layout row>
            <p><font size="4"></font><font siz3="2">(.pdf)</font></p>
            <upload-button title="" name="report" :selectedCallback="Upload"></upload-button>
            </v-layout>
            <br>
        </v-flex>
    </v-container>
</template>

<script>
import api from '../store/api'

 export default {
     methods:{
        Upload(genre, file)
        {
            let formdata = new FormData();
            formdata.append('title', genre);
            formdata.append('file', file);
            formdata.append('classID',-1);
            let token = localStorage.getItem('token')
            api.articleUpdate(token, -1, formdata).then(res=>{
                alert('')
                window.location.reload();
            }).catch(error=>{
                alert(error);
            })
        } 
     },

  }
</script>

<style>

</style>



