<template>
    <v-container>
        <v-layout column="">
            <v-flex v-bind:key=item.url v-for="item in toolbar">
                 <v-btn  @click="changeRoute(item.url)">{{item.genre}}</v-btn>
            </v-flex>
        </v-layout>
    </v-container>
</template>

<script>
export default {
    props:['path'],
    data(){
        return {
            toolbar:[
                {genre:"",url:"home"},
                {genre:"",url:"about"},
                {genre:"",url:"product"},
                {genre:"",url:"download"},
                {genre:"",url:"industry"},
                {genre:"",url:"news"},
                {genre:"EDM",url:"dm"},
                {genre:"",url:"technology"},
                {genre:"",url:"scene"},
                {genre:"",url:"link"},
                {genre:"",url:"message"},
                {genre:"",url:"recommend"},
                {genre:"",url:"brands"},
                {genre:"",url:"connection"},
                {genre:"",url:"ask"},
                {genre:"",url:"order"},
                {genre:"",url:"member"},
                {genre:"",url:"membercheck"},
                {genre:"",url:"newsletter"}
            ]
        }
    },
    methods:{
        changeRoute(url){
            this.$router.push('/home/'+ url);
        }
    }
}
</script>

<style>

</style>



